<template>
    <!-- Start Works Area -->
    <section class="works-area ptb-80 bg-f7fafd">
        <div class="container">
            <div class="section-title">
                <h2>Productos</h2>
                <div class="bar"></div>
            </div>

            <carousel 
                ref="productDetails"
                :autoplay = "true"
                :navigationEnabled="!isMobile"
                :paginationEnabled = "isMobile"
                :loop = "true"
                :autoplaySpeed = "true"
                :autoplayTimeout = "5000"
                :perPageCustom = "[[0, 1],[768, 2],[1200, 3], [1500, 4]]"
                :navigation-next-label="navigationNext" 
                :navigation-prev-label="navigationPrev"
                style="margin:0 -15px"
            >
                <slide v-for="(item,idx) in products" :key="idx">
                    <div class="single-products">
                      <div class="products-image">
                          <img :src="require('../../../assets/img/'+item.img_article)" :alt="item.title">
                      </div>

                      <div class="products-content">
                          <h3>
                              <router-link to="/details">{{item.title}}</router-link>
                          </h3>
                          <!--<a 
                              href="javascript:void(0)" 
                              class="add-to-cart-btn"
                          >
                              Ver detalle
                          </a>-->
                      </div>
                  </div>
                </slide>
            </carousel>
        </div>
    </section>
    <!-- End Works Area -->
</template>

<script>
import { Carousel, Slide } from 'vue-carousel';
import products from '../../../assets/products'
import ChevronLeft from '../../../assets/img/carousel/prev.svg'
import ChevronRight from '../../../assets/img/carousel/next.svg'
export default {
    name: 'Works',
    data(){
      return{
        products
      }
    },
    components: { Carousel, Slide },
    computed: {
        navigationNext() {
          const chevronRight = ChevronRight;
          return `<img style="opacity:0.5" src="${chevronRight}"/>`;
        },
        navigationPrev() {
          const chevronLeft = ChevronLeft;
          return `<img style="opacity:0.5" src="${chevronLeft}"/>`;
        },
        isMobile(){
          if( screen.width <= 760 ) {
                    return true;
                }
                else {
                    return false;
                }
        }
    }
}
</script>