
export default {
    // Ya no uso funciones extra, pero dejo esto acá por si acaso 
    toTitleCase (snake_case_title) {
        let title = snake_case_title.toLowerCase();
        let arr_split = title.split("_");
        let arr_join = [];
        arr_split.forEach(e => arr_join.push(e.charAt(0).toUpperCase() + e.slice(1)));
        title = arr_join.join(" ");
        
        return title;
    }
}