<script>
import products from '../../../assets/products';
import services from '../../../assets/services';
import components from '../../../assets/components';

export default {
    name: 'BlogDetails',
    data: () => ({
        item: { title: '' },
        item_type: ''
    }),
    mounted() {
        this.getItem();
    },
    watch: {
        '$route.params' () {
            this.getItem();
        }
    },
    methods: {
        getItem() {
            //console.log("=> param: ", this.$route.params.name);
            let param = this.$route.params.name;
            let r_product = products.find(e => e.path == param);
            let r_service = services.find(e => e.path == param);
            let r_components = components.find(e => e.path == param);
            this.item = r_product? r_product : (r_service? r_service : (r_components? r_components:null));
            this.item_type = r_product? "producto" : (r_service? "servicio" : (r_components? "componente":''));
            //console.log("=> item: ", this.item);
        },
        getMoreitems(){
            let param = this.$route.params.name;
            return (this.item_type == "producto"?
                        products.filter(e => e.path != param) :
                    (this.item_type == "servicio"?
                        services.filter(e => e.path != param) :
                    (this.item_type == "componente"?
                        components.filter(e => e.path != param) :
                        []
                    )));
        }
    },
    beforeRouteEnter: (to, from, next) => {
        let param = to.params.name;
        let result_products = products.find(e => e.path == param);
        let result_services = services.find(e => e.path == param);
        let result_components = components.find(e => e.path == param);
        if (result_products || result_services || result_components) next() 
        else next('/not-found');
    },
    beforeRouteUpdate: (to, from, next) => {
        let param = to.params.name;
        let result_products = products.find(e => e.path == param);
        let result_services = services.find(e => e.path == param);
        let result_components = components.find(e => e.path == param);
        if (result_products || result_services || result_components) next() 
        else next('/not-found');
    }
}
</script>